Id }}</code></td>\r\n\t\t\t\t</tr>\r\n\t\t\t\t<tr>\r\n\t\t\t\t\t<td>{{ t('oauth2', 'Secret') }}</td>\r\n\t\t\t\t\t<td><code>{{ renderedSecret }}</code><a class=\"icon-toggle has-tooltip\" :title=\"t('oauth2', 'Show client secret')\" @click=\"toggleSecret\" /></td>\r\n\t\t\t\t</tr>\r\n\t\t\t</table>\r\n\t\t</td>\r\n\t\t<td class=\"action-column\">\r\n\t\t\t<NcButton type=\"tertiary-no-background\"\r\n\t\t\t\t:aria-label=\"t('oauth2', 'Delete')\"\r\n\t\t\t\t@click=\"$emit('delete', id)\">\r\n\t\t\t\t<template #icon>\r\n\t\t\t\t\t<Delete :size=\"20\"\r\n\t\t\t\t\t\t:title=\"t('oauth2', 'Delete')\" />\r\n\t\t\t\t</template>\r\n\t\t\t</NcButton>\r\n\t\t</td>\r\n\t</tr>\r\n</template>\r\n\r\n<script>\r\n\r\nimport Delete from 'vue-material-design-icons/Delete'\r\nimport NcButton from '@nextcloud/vue/dist/Components/NcButton'\r\n\r\nexport default {\r\n\tname: 'OAuthItem',\r\n\tcomponents: {\r\n\t\tDelete,\r\n\t\tNcButton,\r\n\t},\r\n\tprops: {\r\n\t\tclient: {\r\n\t\t\ttype: Object,\r\n\t\t\trequired: true,\r\n\t\t},\r\n\t},\r\n\tdata() {\r\n\t\treturn {\r\n\t\t\tid: this.client.id,\r\n\t\t\tname: this.client.name,\r\n\t\t\tredirectUri: this.client.redirectUri,\r\n\t\t\tclientId: this.client.clientId,\r\n\t\t\tclientSecret: this.client.clientSecret,\r\n\t\t\trenderSecret: false,\r\n\t\t}\r\n\t},\r\n\tcomputed: {\r\n\t\trenderedSecret() {\r\n\t\t\tif (this.renderSecret) {\r\n\t\t\t\treturn this.clientSecret\r\n\t\t\t} else {\r\n\t\t\t\treturn '****'\r\n\t\t\t}\r\n\t\t},\r\n\t},\r\n\tmethods: {\r\n\t\ttoggleSecret() {\r\n\t\t\tthis.renderSecret = !this.renderSecret\r\n\t\t},\r\n\t},\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n\t.icon-toggle,\r\n\t.icon-delete {\r\n\t\tdisplay: inline-block;\r\n\t\twidth: 16px;\r\n\t\theight: 16px;\r\n\t\tpadding: 10px;\r\n\t\tvertical-align: middle;\r\n\t}\r\n\ttd code {\r\n\t\tdisplay: inline-block;\r\n\t\tvertical-align: middle;\r\n\t}\r\n\ttable.inline td {\r\n\t\tborder: none;\r\n\t\tpadding: 5px;\r\n\t}\r\n</style>\r\n"],"sourceRoot":""